import { Component, ViewChild, ElementRef, AfterViewInit, OnInit } from '@angular/core';
import { HttpClient, } from '@angular/common/http';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-ocr-page',
  templateUrl: './ocr-page.component.html',
  styleUrls: ['./ocr-page.component.scss']
})
export class OcrPageComponent implements OnInit {

  @ViewChild('canvasEl', {static: false}) canvasEl: ElementRef;
  @ViewChild('scream', {static: false})  scream: ElementRef;

  selectedFile: File;
  imgURL: any;
  itemList: any[] = [];
  scale: any;



  constructor(
    private httpClient: HttpClient
  ) { }
  ngOnInit() {

  }


  onFileChanged(event) {
    const URL = window.URL;
    const Img = new Image();
    this.selectedFile = event.target.files[0]
    Img.src = URL.createObjectURL(this.selectedFile);
    Img.onload = (e: any) => {
      const height = e.path[0].height;
      const width = e.path[0].width;
      this.scale = e.path[0].width / 800;
    }


    var mimeType = this.selectedFile.type;
    if (mimeType.match(/image\/*/) == null) {
      alert("Only images are supported.");
      return;
    }

    var reader = new FileReader();
    reader.readAsDataURL(this.selectedFile);
    reader.onload = (_event) => {
      this.imgURL = reader.result;

    }


  }

  onUpload() {
    // upload code goes here
    this.httpClient.get('assets/data.json').subscribe(data => {
      const local = data as any;

      console.log(local.result);

      this.itemList = local.result.item_list;

    })
  }
  draw(item) {


    // this.canvasEl.nativeElement.style.height = this.scream.nativeElement.height+'px';
    // this.canvasEl.nativeElement.style.width = this.scream.nativeElement.width+'px';
    let data = item;

    let dataPosition = data.position;
    let position = [];
    dataPosition.forEach((element, index) => {
      element = element / this.scale;
      element = Math.floor(element);
      console.log(element);
      position.push(element);
    });


    // this.context = (this.canvasEl.nativeElement as HTMLCanvasElement).getContext('2d');

    const context: CanvasRenderingContext2D = this.canvasEl.nativeElement.getContext('2d');
    context.clearRect(0, 0, 800, 1500);
    context.beginPath();
    context.lineWidth = 3;
    context.strokeStyle = "orange";
    // this.context.strokeRect(position[0], position[1], position[2]-position[0],position[5]-position[1]);
    console.log(position[0]);
    context.beginPath();
    context.moveTo(position[0], position[1]);
    context.lineTo(position[2], position[3]);
    context.lineTo(position[4], position[5]);
    context.lineTo(position[6], position[7]);
    context.lineTo(position[0], position[1]);
    context.stroke();


  }

}
